import React from 'react';
import cn from 'classnames';
import { Link } from 'gatsby';
import Icon from 'src/components/Icon';
import styles from './styles.module.scss';

const items = [
    {
        id: 'hello',
        label: 'Hello',
        to: '/'
    }
];

const icons = [
    {
        id: 'github',
        href: 'https://github.com/malte-wessel',
        title: 'GitHub'
    },
    {
        id: 'linkedin',
        href: 'https://www.linkedin.com/in/malte-wessel',
        title: 'LinkedIn'
    },
    {
        id: 'twitter',
        href: 'https://twitter.com/drbreak',
        title: 'Twitter'
    }
];

const Navigation = ({ className }) => (
    <nav className={cn(styles.nav, className)}>
        {items.map(({ id, label, to }) => (
            <Link
                key={id}
                to={to}
                className={styles.item}
                activeClassName={styles.itemActive}
            >
                {label}
            </Link>
        ))}
        {icons.map(({ id, href, title }) => (
            <a
                key={id}
                href={href}
                target="_blank"
                className={styles.item}
                title={title}
            >
                <Icon icon={id} />
            </a>
        ))}
    </nav>
);

export default Navigation;
