import random from 'lodash/random';
import { setupCanvas } from './setupCanvas';

const createLine = (size, length, color, width, height) => {
    let x = random(-width * 2, width);
    let y = height + length + random(height * 2);
    let vx = 0;
    let vy = 0;

    let a = random(0.01, 0.5);
    let ax = a;
    let ay = -a;
    let dx = x + 1000;
    let dy = y - 1000;
    let spring = 0.005;

    const reset = () => {
        x = random(-width, width);
        y = height + length + random(length);
        vx = 0;
        vy = 0;

        a = random(0.01, 0.5);
        ax = a;
        ay = -a;
        dx = x + 1000;
        dy = y - 1000;
    };

    const update = (width, height) => {
        vx = (dx - x) * spring;
        vy = (dy - y) * spring;

        vx += ax;
        vy += ay;

        x += vx;
        y += vy;
        if (x > width || y < 0) {
            reset();
        }
    };

    const draw = ctx => {
        ctx.beginPath();
        ctx.moveTo(x, y);
        ctx.lineTo(x + length, y - length);
        ctx.lineWidth = size;
        ctx.strokeStyle = color;
        ctx.stroke();
    };

    return {
        update,
        draw
    };
};

const colors = ['#e4e4e4', '#777777', '#c1c1c1'];

const LINE_COUNT = 200;

export const createAnimation = canvas => {
    let raf;
    let { width, height } = setupCanvas(canvas);
    const ctx = canvas.getContext('2d');

    const lines = new Array(LINE_COUNT);
    for (let i = 0; i < LINE_COUNT; i++) {
        lines[i] = createLine(
            random(0.5, 1.5),
            random(20, 100),
            colors[random(0, colors.length - 1)],
            width,
            height
        );
    }

    const update = () => {
        for (let i = 0, l = lines.length; i < l; i++) {
            lines[i].update(width, height);
        }
    };

    const draw = () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        for (let i = 0, l = lines.length; i < l; i++) {
            lines[i].draw(ctx);
        }
    };

    const loop = () => {
        update();
        draw();
        raf = requestAnimationFrame(loop);
    };

    const resize = () => {
        const dimensions = setupCanvas(canvas);
        width = dimensions.width;
        height = dimensions.height;
        raf = requestAnimationFrame(draw);
    };

    const start = () => {
        raf = requestAnimationFrame(loop);
    };

    const stop = () => cancelAnimationFrame(raf);

    return {
        resize,
        start,
        stop
    };
};
